/* Register.css */

.register-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #3498db;
  }
  
  .register-box {
    width: 400px;
    padding: 40px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    border-radius: 12px;
  }
  
  .register-form {
    display: flex;
    flex-direction: column;
  }
  
  .input-group {
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
  }
  
  label {
    margin-bottom: 10px;
    color: #333;
    flex: 1; /* Make the labels take up the same width */
  }
  
  input {
    padding: 15px;
    font-size: 16px;
    border: 2px solid #3498db;
    border-radius: 6px;
    box-sizing: border-box;
    margin-top: 5px;
  }
  
  button {
    padding: 15px;
    background-color: #e74c3c;
    color: #fff;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 18px;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #c0392b;
  }
  