.donar-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 20px;
  }
  
  .donation-info {
    background-color: #f9f9f9;
    border-radius: 8px;
    padding: 20px;
    margin: 20px 0;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  }
  
  .donation-info h2 {
    color: #3498db;
    margin-bottom: 10px;
  }
  
  .donation-info p {
    line-height: 1.6;
    margin-bottom: 15px;
  }
  
  .btn1,
  .btn2 {
    margin-bottom: 20px;
  }
  
  .neon-button {
    text-decoration: none;
    color: #fff;
    padding: 12px 24px;
    background-color: #3498db;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(52, 152, 219, 0.7);
    transition: background-color 0.3s;
  }
  
  .neon-button:hover {
    background-color: #2980b9;
  }
  